/* ===== Global reveal animation: blur -> clear, smooth and elastic ===== */
@keyframes revealBlurLift {
  0% { opacity: 0; filter: blur(12px); transform: translateY(16px); }
  60% { opacity: 1; filter: blur(2px); transform: translateY(-2px); }
  100% { opacity: 1; filter: blur(0); transform: translateY(0); }
}

/* apply reveal only when enabled on container */
.main-content.reveal-ready > * { animation: revealBlurLift .8s cubic-bezier(.22,1,.36,1) forwards; }

/* Stagger for first 12 items */
.main-content.reveal-ready > *:nth-child(1) { animation-delay: 0ms; }
.main-content.reveal-ready > *:nth-child(2) { animation-delay: 80ms; }
.main-content.reveal-ready > *:nth-child(3) { animation-delay: 160ms; }
.main-content.reveal-ready > *:nth-child(4) { animation-delay: 240ms; }
.main-content.reveal-ready > *:nth-child(5) { animation-delay: 320ms; }
.main-content.reveal-ready > *:nth-child(6) { animation-delay: 400ms; }
.main-content.reveal-ready > *:nth-child(7) { animation-delay: 480ms; }
.main-content.reveal-ready > *:nth-child(8) { animation-delay: 560ms; }
.main-content.reveal-ready > *:nth-child(9) { animation-delay: 640ms; }
.main-content.reveal-ready > *:nth-child(10) { animation-delay: 720ms; }
.main-content.reveal-ready > *:nth-child(11) { animation-delay: 800ms; }
.main-content.reveal-ready > *:nth-child(12) { animation-delay: 880ms; }

/* Hero left inner sequence */
.hero-left.reveal-seq > * { opacity: 0; filter: blur(8px); transform: translateY(10px); animation: revealBlurLift .8s cubic-bezier(.22,1,.36,1) forwards; }
.hero-left.reveal-seq > *:nth-child(1){ animation-delay: 0ms; }
.hero-left.reveal-seq > *:nth-child(2){ animation-delay: 80ms; }
.hero-left.reveal-seq > *:nth-child(3){ animation-delay: 160ms; }
.hero-left.reveal-seq > *:nth-child(4){ animation-delay: 240ms; }
.hero-left.reveal-seq > *:nth-child(5){ animation-delay: 320ms; }
